import React, {useState , useEffect} from 'react';
import { alpha, makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import InputBase from '@material-ui/core/InputBase';
import Badge from '@material-ui/core/Badge';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import MenuIcon from '@material-ui/icons/Menu';
import SearchIcon from '@material-ui/icons/Search';
import AccountCircle from '@material-ui/icons/AccountCircle';
import MailIcon from '@material-ui/icons/Mail';
import NotificationsIcon from '@material-ui/icons/Notifications';
import MoreIcon from '@material-ui/icons/MoreVert';
import Icon from '@material-ui/core/Icon';
import SaveIcon from '@material-ui/icons/Save';
import Button from '@material-ui/core/Button';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import MutiSelect from './MutiSelect'
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';
import { Grid } from '@material-ui/core';
import { Card } from '@material-ui/core';
import { MuiThemeProvider, useTheme } from '@material-ui/core';
import { TablePagination } from '@material-ui/core';
import MUIDataTable from 'mui-datatables';
import BaseUrl from '../constant'
import axios from 'axios';


const useStyles = makeStyles((theme) => ({
  grow: {
    flexGrow: 1,
  },


}));


const ProductList = (props) => {
  const [open, setOpen] = useState(false);
  const [productList, setProductList] = useState([]);


  

  const columns = [
    {
      name: 'p_name',
      label: 'Product',
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`
      }
    },
    {
      name: 'variant',
      label: 'Variant',
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`
      }
    },
    {
        name: 'location',
        label: 'Location',
        options: {
          filter: true,
          sort: true,
          customBodyRender: (value, tableMeta, updateValue) => `${value}`
        }
      },
      {
        name: 'recordQty',
        label: 'Qty.In Stock',
        options: {
          filter: true,
          sort: true,
          customBodyRender: (value, tableMeta, updateValue) => `${value}`
        }
      },
      {
        name: 'Price',
        label: 'Price',
        options: {
          filter: true,
          sort: true,
          customBodyRender: (value, tableMeta, updateValue) => `${value}`
        }
      },
      {
        name: 'Cost',
        label: 'Cost',
        options: {
          filter: true,
          sort: true,
          customBodyRender: (value, tableMeta, updateValue) => `${value}`
        }
      },
  ];

  const option = {
    filterType: 'dropdown',
    selectableRowsHideCheckboxes: true,
    pagination: false
  };

  useEffect( () => {
    axios.get(BaseUrl +'getAllProducts')
    .then(function (response) {
      console.log("response", response.data.doc);
            let newData = [];
      
       let product = response.data.doc.map((item)=>{
         let variant = `Barcode ${item.Barcode} SKU ${item.Sku}`
         return {p_name:item.productName,p_id:item._id,variant,...item}
     })
    console.log("product",product);
    newData = [...newData,...product]
     console.log("newData", newData);
    setProductList(newData)
    })
    .catch(function (error) {
      console.log(error);
    });

  }, []);
      



  return (
    <div className="container">
      <div>
      <Grid item xs={12} sm={12}>
            <Card>
              <MuiThemeProvider >
                <MUIDataTable
                  style={{ th: { backgroundColor: 'grey' } }}
                  //   title="Employees List"
                  data={productList}
                  columns={columns}
                  options={option}
                />
                <TablePagination
                  component="div"
                  // count={totalCount}
                  // page={page}
                  // onPageChange={handleChangePage}
                  // rowsPerPage={rowsPerPage}
                  // onRowsPerPageChange={handleChangeRowsPerPage}
                />
              </MuiThemeProvider>
            </Card>
          </Grid>
      </div>






    </div>
  );
}
export default ProductList;